import React, { forwardRef } from "react";
import { Loader2 } from "lucide-react";
import type { ButtonProps } from "./types";
import { getBaseStyles, getVariantStyles } from "./styles";
import { getSizeStyles, getRadiusStyles, getWidthStyles } from "./utils";

/**
 * A customizable Button component with variants, colors, icons, and loading states.
 */

const Button = forwardRef(
  (
    {
      children,
      variant = "solid",
      color = "primary",
      size = "md",
      pill = false,
      block = false,
      disabled = false,
      loading = false,
      active = false,
      leftIcon,
      rightIcon,
      onClick,
      type = "button",
      className = "",
      ...props
    }: ButtonProps,
    ref: React.Ref<HTMLButtonElement>
  ) => {
    const allStyles = [
      ...getBaseStyles(),
      ...getSizeStyles(size),
      ...getRadiusStyles(size, pill),
      ...getWidthStyles(block),
      ...getVariantStyles(variant, color, active),
      ...(disabled
        ? ["opacity-50", "cursor-not-allowed", "pointer-events-none"]
        : []),
      ...(loading ? ["cursor-wait"] : []),
      className,
    ]
      .filter(Boolean)
      .join(" ");

    return (
      <button
        ref={ref}
        type={type}
        className={allStyles}
        onClick={onClick}
        disabled={disabled || loading}
        {...props}>
        {loading ? (
          <Loader2
            className='animate-spin'
            size={size === "xs" ? 14 : size === "sm" ? 16 : 18}
          />
        ) : (
          leftIcon && <span className='mr-1'>{leftIcon}</span>
        )}
        {children && <span>{children}</span>}
        {!loading && rightIcon && <span className='ml-1'>{rightIcon}</span>}
      </button>
    );
  }
);

Button.displayName = "Button";
export default Button;
